<style scoped>
  .layout {
    background: #f5f7f9;
    position: relative;
  }

  .layout-copy {
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }

  .layout-menu-left {
    background: #464c5b;
    position: fixed;
    top: 0;
    bottom: 0;
  }
</style>
<template>
  <div class="layout">
    <Row type="flex" class="layout-menu-row">
      <i-col span="3" class="layout-menu-left">
        <Menu theme="dark" width="auto" :open-names="['1']">
          <div class="layout-logo-left"></div>
          <Submenu name="1">
            <template slot="title">
              <Icon type="ios-navigate"></Icon>
              导航一
            </template>
            <Menu-item @click.native="scroll(item.title)" v-for='(item,index) in navList' key='index' :name='`1+${index}`'>{{item.title}}</Menu-item>
          </Submenu>
        </Menu>
      </i-col>
      <i-col span="21" offset="3">
        <first-page></first-page>
        <div class="layout-copy">
          2016-2017 &copy; Roger
        </div>
      </i-col>
    </Row>

  </div>
</template>
<script>
  import First from './page/first'
  import navList from './data/_list.json'
  export default {
    components: {
      'first-page': First
    },
    data() {
      return {
        navList,
      }
    },
    methods: {
      scroll(id) {
        var win = window;
        var to = document.querySelector(`#${id}`).offsetTop
        var scrollTop = 0;
        var speed;
        window.requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame || window.msRequestAnimationFrame;

        if (document.documentElement && document.documentElement.scrollTop) {
          scrollTop = document.documentElement.scrollTop;
        } else if (document.body) {
          scrollTop = document.body.scrollTop;
        }

        if (scrollTop === to) { return }
        
        speed = (to - scrollTop) / 20;

        function move() {
          scrollTop += speed;
          window.scrollTo(0, scrollTop);
          if (speed > 0 && scrollTop + speed <= to || speed < 0 && scrollTop + speed >= to) {
            requestAnimationFrame(move);
          }
        }

        requestAnimationFrame(move);
      }
    }
  }

</script>


